  "00fc9b84": (_ctx.shadeBlur),
  "c0201732": (_ctx.frontImageUrl),
  "69c621d1": (_ctx.backImageUrl),
  "b2e2ef98": (_ctx.rotation),
  "da6e3ad0": (_ctx.opacityShade),
  "9a76534e": (_ctx.rotationShade),
  "5891657b": (_ctx.rotationOutsize)
